<template>
    <div id="container">
     <swiper :options="swiperOption" ref="mySwiper" class="banner-swiper">
      <swiper-slide class="swiper-slide games">
       <img src="../assets/img/banner01.jpg" alt="">
      </swiper-slide>
      <swiper-slide class="swiper-slide games">
       <img src="../assets/img/banner02.jpg" alt="">
      </swiper-slide>
      <swiper-slide class="swiper-slide games">
       <img src="../assets/img/banner03.jpg" alt="">
      </swiper-slide>
      <swiper-slide class="swiper-slide games">
       <img src="../assets/img/banner04.jpg" alt="">
      </swiper-slide>
      <div class="swiper-pagination " slot="pagination"></div>
      <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
      <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
     </swiper>   
    </div> 
</template>
    <script>
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
     export default {
      name: 'carrousel',
      data () {
       return {
        swiperOption: {
            autoplay : {
          disableOnInteraction: false, //用户操作后是否禁止自动循环
          delay: 1000 //自自动循环时间
         }, //可选选项，自动滑动
         speed: 1000, //切换速度，即slider自动滑动开始到结束的时间（单位ms）
         loop:true, //循环切换
         grabCursor: true, //设置为true时，鼠标覆盖Swiper时指针会变成手掌形状，拖动时指针会变成抓手形状
         // setWrapperSize: true, //Swiper使用flexbox布局(display: flex)，开启这个设定会在Wrapper上添加等于slides相加的宽或高，在对flexbox布局的支持不是很好的浏览器中可能需要用到。
         autoHeight: true,   //自动高度。设置为true时，wrapper和container会随着当前slide的高度而发生变化。   
         scrollbar: '.swiper-scrollbar',
         mousewheelControl: false, //设置为true时，能使用鼠标滚轮控制slide滑动
         observeParents: true, //当改变swiper的样式（例如隐藏/显示）或者修改swiper的子元素时，自动初始化swiper
     
         pagination: {
          el: '.swiper-pagination',
          // type : 'progressbar', //分页器形状
          clickable :true, //点击分页器的指示点分页器会控制Swiper切换
         },
         navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
          hiddenClass: 'my-button-hidden',
         }
       },
       computed: {
        swiper() {
         return this.$refs.mySwiper.swiper
        }
       }
      }
     }
    }
    </script>
    <style>
    .banner-swiper{
        width:100%;
        border-radius: 10px;
    } 
    .banner-swiper img{
        width:100%;
        height:auto; 
    }
    .banner-swiper .swiper-pagination .swiper-pagination-bullet{
        width:30px;
        height:2px;
        border-radius: 0;
    }
    .banner-swiper .swiper-pagination .swiper-pagination-bullet-active{
        background: #ffffff;
    }
    .banner-swiper .swiper-button-prev{
        left:0;
        top:49%;
        padding:2px;
        background-color: rgba(0, 0, 0, 0.3);
        background-size: 16px auto;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }
    .banner-swiper .swiper-button-next{
        right:0;
        top:49%;
        padding:2px;
        background-color: rgba(0, 0, 0, 0.3);
        background-size: 16px auto;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }
    </style>


