<template>
<div class="list-box">
    <ul class="list">
        <li v-for="list in item" >
            <h1  @click="active" ref="listTitle" :class="{'list-title-active':listactive}">{{list.title}}</h1>
            <p ref="listContent" :class="{'list-active':listactive}">{{list.content}}</p>
        </li>
    </ul>
    <ul class="list-img">
        <li v-for="list in item">
            <img :src="list.image" alt="" />
        </li>
    </ul>
    
</div>

