                {"title":"Kung Fu Panda 2","content":"In the Valley of Peace, Po Ping is revelling in his fulfilled dreams as he serves as the fabled Dragon Warrior protecting his home with his heroes now his closest friends. However, Po and company learn that the murderous Lord Shen of Gongman City is threatening the land with a fearsome new weapon that could mean the end of kung fu forever.","image":"https://baizhiedu-ztnew.oss-cn-hangzhou.aliyuncs.com/pc2017/img/recommend1.jpg"},
                {"title":"The Dark Knight","content":"Batman raises the stakes in his war on crime. With the help of Lieutenant Jim Gordon and District Attorney Harvey Dent, Batman sets out to dismantle the remaining criminal organizations that plague the city streets. But they soon find themselves prey to a reign of chaos unleashed by a rising criminal mastermind known to the terrified citizens of Gotham as The Joker.","image":"https://baizhiedu-ztnew.oss-cn-hangzhou.aliyuncs.com/pc2017/img/recommend1.jpg"},
