            e.target.className="list-title-active";
            e.target.nextElementSibling.className="list-active";

        }
    },
    computed: {
        
    }
}
</script>
<style scoped>
.list-box{
    width:700px;
    height:390px;
    border:10px solid orange;
    box-shadow: 0 0 10px #cccccc;
    margin:0 auto;
}
.list-box ul{
    float: left;
}
.list-box .list{
    width:250px;
}
.list-box .list h1{
    padding:10px;
    background: rgb(238, 237, 237);
    border-bottom:1px solid #333333;
    width:inherit
}
.list-box .list p{
    display: none;
}
.list-box .list .list-title-active{
    background: orange;
    color:#ffffff;
    border:none;
}
.list-box .list .list-active{
    display: block;
    width:100%;
    height:260px;
    overflow: hidden;
}
.list-box .list-img{
    width:450px;
}
.list-box .list-img img{
    display: none;
    width:100%;
    height:390px;
}
.list-box .list-img-active{
    display: block;

}
</style>


