import Vue from 'vue'
import Router from 'vue-router'
import home from '@/page/home'
import articleItem from '@/page/articleItem'
import week from '@/page/week'
import myfunction from '@/page/myfunction'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/article',
      name: 'articleItem',
      component: articleItem
    },
    {
      path:'/week',
      name:'week',
      component:week
    },
    {
      path:'/myfunction',
      name:'myfunction',
      component:myfunction
    }
  ]
})



