<template>
<div class="right-hotter">
    <ul >
        <li class="hotter-week"><router-link to="/week" >7日热门</router-link><img src="../assets/img/hot.png" alt="" /></li>
        <li class="hotter-month"><router-link to="/week" >30日热门</router-link></li>
        <li class="hotter-serialize"><router-link to="/week" >优选连载</router-link></li>
    </ul>
    <div class="QRcode">
        <img class="QRcode-small" src="../assets//img/weixin.png" alt="" title="你好啊"/>
        <img class="QRcode-big" src="../assets//img/weixin.png" alt="" title="你好啊"/>
        <h1>小五微信,鼠标停留查看大图</h1>
        <p>前端技术学习，非诚勿扰</p>
    </div>
</div>
</template>hotter-week

<script>
export default{
    name:'RightHotter'
}
</script>

<style scoped>
.right-hotter ul li{
    position: relative;
    width:100%;
    height:50px;
    border-radius:5px;
    margin-bottom:10px;
    text-indent: 30px;
    line-height: 50px;
}
.right-hotter .hotter-week{
    background: orange;
}
.right-hotter .hotter-week img{
    position: absolute;
    right:10px;
    top:-7px;
    width:auto;
    height:50px;
    vertical-align: middle;
    opacity:0.6;
}
.right-hotter .hotter-month{
    background: orchid;
}
.right-hotter .hotter-serialize{
    background: rgb(150, 187, 75);
}
.right-hotter a{
    color:#ffffff;
    font-size:16px;
    font-weight: bold;  
}
.right-hotter .QRcode{
    position: relative;
    width:100%;
    border:1px solid #cccccc;
    border-radius:5px;
    text-indent:5rem;
    padding:20px 0;
}
.right-hotter .QRcode .QRcode-small{
    position: absolute;
    top:0;
    bottom:0;
    left:10px;
    margin:auto;
    width:60px;
    height:60px;
}
.right-hotter .QRcode .QRcode-big{
    display: none;
    position: absolute;
    top:-240px;
    right:0;
    left:0;
    margin:auto;
    width:230px;
    height:230px;
    border: 1px solid rgb(83, 83, 83);
    border-radius: 5px;
}
.right-hotter .QRcode:hover .QRcode-big{
    display: block;
}
.right-hotter .QRcode .QRcode-big::after{
    content:'';
}
.right-hotter .QRcode h1{
    font-size:15px;
    color:#333;
}
.right-hotter .QRcode p{
    font-size:14px;
    color:#999;
    line-height:30px
}

</style>


