<template>
<div>
    <div>
        <ul class="article-list">
            <li v-for="sit in item">
                <div>            
                    <h1><router-link :to="url+sit.id">{{sit.title}}</router-link></h1>
                    <p class="article-list-content" >{{sit.content}}</p>
                    <p class="article-list-author"><span>发布时间：{{sit.time}}</span><span><i>&#9993</i>{{sit.id}}</span></p>
                </div>
                
                <img :src="sit.img" alt="" />
            </li>
        </ul>
    </div>
    <ul class="article-list">
        <li v-for="item in list" >
            <div>            
                <h1><router-link to="/article">{{item.title}}</router-link></h1>
                <p>{{item.abstract}}</p>
                <p class="article-list-author"><span>{{item.author}}</span><span><i>&#9993</i>{{item.comment}}</span><span><i>&#10084;</i>{{item.collect}}</span></p>
            </div>
            <img :src="item.img" alt="" />
        </li>
    </ul>

</div>
</template>

<script>
export default{
    name:'ArticleList',
    data () {
        return {
            list:[
                {"title":"日常练习钢笔字第九天","abstract":"最近练习写字心境确实安静了不少，不那么总是感觉着急，也不那么因为感情而悲伤，八个月了，就那样过去吧，不要再去怀念，不要再去感伤，我应该有个新的开","author":"寒山月下","comment":85,"collect":90,"img":"../../static/img/atricle-list-01.jpg"},
                {"title":"这样喝茶，等于慢性割肾","abstract":"最近练习写字心境确实安静了不少，不那么总是感觉着急，也不那么因为感情而悲伤，八个月了，就那样过去吧，不要再去怀念，不要再去感伤，我应该有个新的开","author":"nmyanwensheng","comment":67,"collect":80,"img":"../../static/img/atricle-list-02.jpg"},
                {"title":"可笑的邂逅","abstract":"现在我才明白天空的美人人可以抬头山就在那里谁都可以往高处走去 但是人啊千万不要高估自己悬梁刺股的游戏状元服尺寸却不对 你亲爱的皇帝刚刚看见过你只","author":"百样生活","comment":25,"collect":90,"img":"../../static/img/atricle-list-03.jpg"},
                {"title":"一个新颖的兼职项目，日入几百，你也可以干","abstract":"今天就分享一个线下项目，比较适合用来做兼职。 只要面子上过得去，一天挣个300块钱还是可以的。 这个项目就是卖气球。 不过，不是我们平常在街上见...","author":"网赚项目大师","comment":45,"collect":100,"img":"../../static/img/atricle-list-04.jpg"},
                {"title":"泡富婆（140字微小说）","abstract":"高大帅气的穷男友竟然为了钱去泡富婆！ 女人提分手并迅速嫁给一老板。 婚后生活富足安乐，可女人总觉得缺了什么！ 直到与开大奔的前男友偶遇，他说自己...","author":"桃小毛","comment":32,"collect":58,"img":"../../static/img/atricle-list-05.jpg"},
            ],
            mes:"测试数据",
            url:'/article?id=',
            img_rul:'../../static/img/atricle-list-02.jpg',
            item:[]
        }
    },
    mounted(){
    this.$axios
      .get('http://www.baizhiedu.com/baizhi-news')
      .then(res => {
          let data= res.data.filter(el=>{
              if(el.id !==null){
                  return el
              }   
          })
          data.map(x=>{
              let star_img=x.content.indexOf("<img");
              if(star_img>0){
                let star_url=x.content.indexOf("src=\"",star_img)+5;
                let end_url=x.content.indexOf("\"",star_url);
                let str=x.content.slice(star_url,end_url);
                console.log(str);
                x.img=str;
              }else{
                x.img=this.img_rul;
              }
              x.content=x.content.replace(/<([^<>]*)>/g,"");
          });
          this.item=data;
      }) 
      .catch(error =>{
           console.log(error);
        });
           
    },
    methods:{
       
    }
}
</script>

<style scoped>
.article-list{
    width:100%;
    margin-top:30px;
    border-bottom:1px solid #f0f0f0;
} 
.article-list li{
    padding:20px 0;
    border-top:1px solid #f0f0f0;
    position: relative;
}
.article-list li div{
    width:73%;
}
.article-list li h1{
    line-height:36px;
}
.article-list li h1 a{
    font-size:18px;
    font-weight: bold;
    color:black;
}
.article-list li h1 a:hover{
    text-decoration:underline;
}

.article-list li p{
    font-size:13px;
    color:#999;
    font-family:Microsoft YaHei,Arial, Helvetica, sans-serif;
    line-height: 24px;
}
.article-list li .article-list-content{
    height:50px;
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow:ellipsis; 
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}
.article-list li .article-list-author{
    font-size:12px;
    color:#999;
    margin-top:6px;
}
.article-list li .article-list-author span{
    padding-right:10px;
}
.article-list li .article-list-author span i{
    padding-right:2px;
}
.article-list li .article-list-author span:hover{
    color:rgb(105, 104, 104);
}
.article-list li img{
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin:auto 0;
    width:23%;
    height:auto;
    border-radius: 5px;
}
</style>


