// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import axios from 'axios'

//global components
import HomeHeader from '@/components/HomeHeader'
import HomeFooter from '@/components/HomeFooter'
import ArticleList from '@/components/ArticleList'
import RightHotter from '@/components/RightHotter'
import RightMenu from '@/components/RightMenu'
import Calendar from '@/components/Calendar'

Vue.component('HomeHeader',HomeHeader)
Vue.component('ArticleList',ArticleList)
Vue.component('HomeFooter',HomeFooter)
Vue.component('RightHotter',RightHotter)
Vue.component('RightMenu',RightMenu)
Vue.component('Calendar',Calendar)

//public css
import './assets/css/reset.css'
import 'swiper/dist/css/swiper.css'

Vue.use(VueAwesomeSwiper)
Vue.config.productionTip = false;
Vue.prototype.$axios = axios;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { 
    App
  },
  template: '<App/>'
})




