<template>
<div>
    <HomeHeader></HomeHeader>
    <div class="container clearfix">
        <div class="container-left">
            <h1 class="article-title">{{articleTitle}}</h1>
            <p class="article-subhead"><span>发布时间 {{ articleTime }}</span><span>字数 {{articleLength}} </span><span>阅读 {{articleRead}}</span><span>评论 {{articleComment}}</span><span>喜欢 {{articleCollect}}</span></p>
            <div class="article-content">
                <p v-html="articleConent"></p>

            </div>   
        </div>
        <div></div>
        <div class="container-right">
            <RightMenu></RightMenu>
        </div>
    </div>
    <HomeFooter></HomeFooter>
</div>
</template>
<script>
export default {
    name:'articleApp',
    data () {
        return {
            articleTime:'2018.09.22 21:18',
            articleLength:'2253',
            articleRead:'4789',
            articleComment:'6',
            articleCollect:'50',
            articleConent:"<p>3月12日，新西兰奥克兰区块链公司Centrality宣布，已经与万达旗下网络科技集团达成合作，双方将分两步在区块链技术、应用落地、生态圈打造等方面展开战略技术合作。据了解，此次合作早已在年前完成，流程走完了才发的消息。</p><p>万达强调：此次合作不涉及ICO，但是更具体的技术买卖、合作开发后技术的归属以及预期项目落地时间问题并没有明确回应。在布局区块链长达2年、国内外区块链技术企业早已激烈抢占制高点之后，作为商业巨头的万达才开始选择与国外公司合作、进行所谓的私有链、联盟链和公链“互补”，但是当前种种迹象表明，万达的区块链布局之路已难掩停滞不前的结局。</p>",
            articleId:'',
            articleTitle:'为什么万达布局区块链，还是难逃败局？'
        }
    },
    mounted () {
    let getId=function(){
        let loacl_url=document.location.href;
        let getId=loacl_url.substr(loacl_url.indexOf("=")+1)
        return getId;
    }  
    this.$axios
        .get('http://www.baizhiedu.com/baizhi-news')
        .then(res=>{
            let data= res.data;
            for(let i=0;i<data.length;i++){
                if(data[i].id==getId()){
                   // alert(data[i].id)
                   this.articleTitle=data[i].title;
                   this.articleConent=data[i].content;
                   this.articleTime=data[i].time;
                   this.articleId=data[i].id;
                }
            } 

        })
    },
    methods:{

    }

}

</script>

<style scoped>
.article-title{
    font-size:34px;
    font-weight: bold;
    color:#333;
    margin-top:40px;
    line-height: 60px;
}
.article-subhead{
    font-size:12px;
    color:#999;
    line-height:20px;
}
.article-subhead span{
    margin-right:10px;
}
.article-content{
    margin-top:50px;
    color:#2f2f2f;
    font-size:16px;
    line-height:30px;
}
.article-hotter{
    margin-top:10px;
}

</style>


