<template>
<dl class="rightmenu">
    <dt>常用分类</dt>
    <dd v-for="item in menu">
        <router-link :to="item.url">{{ item.course }}</router-link>
    </dd>
</dl>
</template>
<script>
export default{
    name:'RightMenu',
    data () {
        return {
            menu:[
                {course:'css3',url:'/myfunction'},
                {course:'h5',url:'/'},
                {course:'javascript',url:'/'},
                {course:'jquery',url:'/'},
                {course:'vue',url:'/'},
            ]
        }
    }
}
</script>

<style scoped>
    .rightmenu{
        width:100%;
        margin-top:30px;
    }
    .rightmenu dt{
        color:#999;
        font-size:14px;
        line-height:30px;
        border-bottom:1px solid #cccccc;
    }
    .rightmenu dd{
        background: rgb(236, 235, 235);
        margin-top:10px;
        padding:10px 0 10px 20px;
        border-radius: 5px;
    }
    .rightmenu dd a{
        display: inline-block;
        color:#333;
        font-size:14px;
        line-height:24px;

    }
</style>


